.post-detail {
  width: 275px;
  margin-left: 25px;
  margin-top: 25px;
  padding: 20px;
  padding-bottom: 0px;
  display: flex;
  flex-direction: column;
}

.post-link {
  text-decoration: none;
}

.comps {
  width: 275px;

  ul {
    display: flex;
    flex-direction: row;
    list-style: none;
    margin-bottom: 0px;
    .Reading-time {
      margin-right: 25%;
    }
  }
  li {
    padding-left: 10px;
    padding-right: 0px;
    margin-right: 0px;
    font-size: 12px;
    margin-top: 5px;
  }
}

.post-thumbnail {
  margin-top: 2%;
  margin-left: 2%;
  margin-bottom: 15px;
  height: 100px;
  width: 100px;
  object-fit: cover;
  object-fit: cover !important;
  object-position: 50% 50% !important;
}
.post-thumbnail-2 {
  margin-bottom: 15px;
  height: 256px;
  width: 342px;
  object-fit: cover;
  object-fit: cover !important;
  object-position: 50% 50% !important;
}

.ptitle .h :hover {
  background-color: rgb(255, 195, 127);
}

.w {
  display: flex;
  flex-direction: row;
}

.wd {
  display: flex;
  flex-direction: column;
  font-size: smaller;
  img {
    margin-right: 12px;
  }
}
.wds {
  display: flex;
  flex-direction: column;
  font-size: smaller;
  img {
    margin-right: 12px;
  }
}
.wb {
  display: flex;
  flex-direction: column;
  font-size: small;
  padding-right: 10px;
}
.-x- {
  display: flex;
  flex-direction: row;
}
