.comments-likes {
  display: flex;
  justify-content: flex-end;
  span {
    font-family: "Average Sans";
    margin-right: 10px;
  }
  h7 {
    font-size: 12px;
  }
}

.date {
  font-size: 12px;
  font-style: none;
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  line-height: 24px;
  color: #b6b6b6;
}

.postImage {
  object-fit: cover;
  object-fit: cover !important;
  object-position: 50% 50% !important;
    width: 100%;
  //   height: 100%;
}

.comment-action {
  padding-left: 8px;
  cursor: "auto";
}

[class*="-col-rtl"] .comment-action {
  padding-right: 8px;
  padding-left: 0;
}

.comment-section {
  margin-top: 20px;
  margin-bottom: 30px;
}

.content {
  margin-top: 30px;
}
